body, h1, h2, h3, h4, h5, h6 { margin: 0 }
body {
  height: 100%;
  font-size: 17px;
}


%flex_center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pulsate {
  animation: pulsate 1.3s infinite;
}
@keyframes pulsate {
  0% { 
    transform: scale(0.9);
    box-shadow: 0 0 0 0 rgba(153, 24, 63, 0.7);
  }
  50% { 
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(255, 165, 0, 0);
  }
  100% { transform: scale(1) }
}
.whatsapp {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all .2s ease;
  img { width: 30px }

  &:hover {
    transform: scale(1.1);
  }
}


// scroll
::-webkit-scrollbar{ width: 14px }
::-webkit-scrollbar-track{ background: #b1b1b1 }
::-webkit-scrollbar-thumb{ 
  background: #99183f;
  // background: #2c4381;
  border-radius: 7px;
  &:hover { background-color:#781533 }
}


h3 { margin: 20px 0 }
.content { min-height: calc(100% - 65px - 100px) }
.container {
  padding: 0 10px;
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;
}


// HEADER =============================================================
header {
  background-color: orange;
  height: 65px;
  box-shadow: 0px 9px 5px 0px rgba(34, 60, 80, 0.2);
  

  .header {
    @extend %flex_center;

    div {
      transition: all .2s ease;
      &:hover { transform: scale(1.1) }
      
      a {
        font-size: 24px;
        font-weight: 700;
        color: black;
      }
    }

    &_logo {
      height: 50px;
    }
  }
}




// TOP =============================================================

.top {
  background: rgb(56, 56, 56) url('./img/house.jpg') center center/cover no-repeat;
  height: calc(100vh - 65px);

  &_text{
    text-align: center;
    height: 90%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: radial-gradient(50% 30% at 50% 55%, #000000, rgba(0, 0, 0, 0) 90%);
    h2 {
      padding: 0 15px 10px;
      margin-bottom: 10px;
      border-bottom: 3px solid orange;
    }
   
  }
}

// МЕТАЛЛОЧЕРЕПИЦА ===========================================================
.cards {
  @extend %flex_center;
  flex-wrap: wrap;
  margin-bottom: 15px;

  &_item {
    margin: 10px;
    text-align: center;
    img {
      height: 200px;
    }
  }

}



// MIDDLE ===========================================================

.middle {
  
  &_roofing_materials p { text-indent: 20px }
  
  &_services {
    
    &_cards {
      display: flex;
      flex-wrap: wrap;
      
      &_item {
        width: 25%;
        border: 1px solid white;

        &_imgdiv { height: 300px }
        
        &_text {
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;

          height: 70px;
          margin: 0;
          padding: 10px 5px;
          color: white;
          font-size: 18px;
          background: rgb(72, 16, 33);
        }
      }
    }
    
    .middle_services_title {
      // background: orange;
      
      background: #333;
      color: white;
      text-align: center;
      padding: 20px 0;
      h5 {
        font-size: 30px;
        font-weight: 600;
      }
      span {
        display:block;
        margin-top: 10px;
        font-size: 22px;
      }
    }
  }


  &_sales {
    padding: 40px 0;
    @extend %flex_center;
    justify-content: space-evenly;
    img { width: 250px }
    p { margin-left: 3% }
  }
}


// Портфолио ===========================================================

.portfolio {
  &_doposle {
    @extend %flex_center;
    div:first-child { border-right: 1px solid white }
    img { width: 100% }
    h5 {
      text-align: center;
      background-color: red;
      color: white;
      padding: 10px;
      margin: -5px 0;
    }
  }

  &_slider {
    margin: 20px 0 10px;
    
    &_inner {
      @extend %flex_center;
      &_item {
        height: 500px;
        width: 100%;
        transition: all .2s;
      }

    }

    &_controls {
      @extend %flex_center;
      span {color:rgb(167, 167, 167)}
      div {
        user-select: none;
        display: flex;

        h3 {
          font-size: 2.92rem !important;
          cursor: pointer;
          background-color: #383838;
          color: white;
          border-radius: 50%;
          width: 55px;
          height: 55px;
          text-align: center;
          margin-left: 10px;
          transition: all .2s ease;
          &:hover {
            background-color:#781533;
            transform: scale(1.1);
          }
        }

      }
    }
  }
  
}

// MAPS ===========================================================

.maps {
  margin-top: 20px;

  &_imgs {
    @extend %flex_center;
    justify-content: space-evenly;
    img {
      margin: 20px 0;
      width: 200px;
      border-radius: 10px;
      
      transition: all .2s ease;
      &:hover {
        background-color:rgb(206, 206, 206);
        transform: scale(1.2);
      }
    }
  }

  &_leaflet { height: 500px }
}

div.leaflet-bottom.leaflet-right { display: none }




// FOOTER ===========================================================



footer {
  height: 100%;
  background: #333;
  color: white;
  padding: 25px 0;

  .footer { 
    @extend %flex_center;
    flex-wrap: wrap; 

    &_loca {
      display: flex;
      flex-direction: column;
      p {
        border-top: 2px solid orange;
        margin: 5px 0 0 0;
        font-size: 20px;
        font-weight: 500;
      }
    }
  }

}
// MEDIA =========================================================================
@media (max-width: 1060px) {
  .middle_services_cards_item{ width: 33.3% }
}

@media (max-width: 736px) {
 .top_text h2 {font-size: 40px}
 .portfolio_doposle {flex-wrap: wrap}
}

@media (max-width: 712px) {
  header {
    position: sticky;
    top: 0;
    z-index: 401;
  }
  .cards { justify-content: center }
  .middle_services {margin-top: 20px}
  .middle_services_cards_item{ width: 50% }

  .header_logo { display: none }
  .footer { justify-content: space-around !important}
}

@media (max-width: 666px) {
  .maps_imgs{ 
    flex-wrap: wrap;
    justify-content: center;
  }
  .portfolio_slider_inner_item {height: 380px}
}

@media (max-width: 560px) {
  .top_text h2 {font-size: 34px}
  .top_text h4 {font-size: 28px}

  h3 {font-size: 34px}
  .top {height: 65vh}

  .middle_sales { 
    flex-wrap: wrap;
    flex-direction: column;
  }
  .middle_services_cards_item{ width: 100% }
  .maps_leaflet { height: 400px }

}

@media (max-width: 440px) {
  .container { padding: 0 }
  .middle_sales {margin-top: 20px }
  .portfolio_slider_inner_item { height: 350px }
  // .maps_leaflet { height: 350px }

  .middle_services h5 { font-size: 22px }
}

